import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/shared/Layout';
import SecondaryPageWrapper from '../components/styled/SecondaryPageWrapper';
import TopBanner from '../components/shared/TopBanner';
import Posts from '../components/shared/Posts';

const NewsPage = ({ data }) => {
  const {
    wordpressPage: { acf },
    allWordpressPost: { edges },
  } = data;
  return (
    <Layout theme="secondary">
      <SecondaryPageWrapper>
        <TopBanner heading={acf.banner_heading} image={acf.banner_image} />
        <Posts posts={edges} />
      </SecondaryPageWrapper>
    </Layout>
  );
};

export default NewsPage;

export const query = graphql`
  query newsQuery($page_id: Int = 73) {
    wordpressPage(wordpress_id: { eq: $page_id }) {
      title
      acf {
        banner_heading
        banner_image {
          localFile {
            childImageSharp {
              fluid(maxWidth: 1920) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
    allWordpressPost {
      edges {
        node {
          id
          title
          content
          acf {
            url
          }
        }
      }
    }
  }
`;
