import React from 'react';
import { css } from '@emotion/core';
import { CopyHeading, BodyText } from '../styled/Typography';
import { colors, viewports } from '../../utils/variables';
import Container from '../styled/Container';

const Posts = ({ posts }) => {
  return (
    <Container style={containerCSS}>
      {posts.map(({ node }) => (
        <article css={postCSS} key={node.id}>
          <div css={imagePlaceholderCSS} />
          <div css={postContentCSS}>
            <CopyHeading theme="large">{node.title}</CopyHeading>
            <BodyText>{node.content}</BodyText>
            {node.acf.url && (
              <a
                css={ctaCSS}
                href={node.acf.url}
                target="_blank"
                rel="noopener noreferrer"
              >
                Read More
              </a>
            )}
          </div>
        </article>
      ))}
    </Container>
  );
};

export default Posts;

const containerCSS = css`
  padding: 4.375rem 0 3.125rem;
`;

const postCSS = css`
  border-bottom: 2px ${colors.black} solid;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  padding: 1.5rem 0;

  &:last-of-type {
    border-bottom: 0;
  }

  @media ${viewports.medium} {
    display: flex;
    margin-bottom: 0;
    padding: 3.125rem;
    width: 94%;
  }
`;

const imagePlaceholderCSS = css`
  background: #717171;
  height: 18.125rem;

  @media ${viewports.medium} {
    width: 28%;
  }
`;

const postContentCSS = css`
  display: flex;
  flex-direction: column;
  padding-top: 1.5rem;

  @media ${viewports.medium} {
    padding-top: 0.5rem;
    width: 70%;
  }
`;

const ctaCSS = css`
  color: ${colors.blue};
  font-weight: 700;
  margin-top: auto;
  text-decoration: none;

  &:after {
    content: '>';
    display: inline-block;
    margin-left: 0.25rem;
    position: relative;
    transform: translate3d(0, 0, 0);
    transition: transform 300ms ease-out;
  }

  @media ${viewports.medium} {
    &:hover {
      color: ${colors.darkBlue};

      &:after {
        transform: translate3d(0.3rem, 0, 0);
      }
    }
  }
`;
